<template>
  <transition 
    v-bind:css="false"
    v-on:enter="enter"
    v-on:leave="leave"
    >
    <div class="list-player" v-show="isShowListPlayer">
      <div class="player-warpper">
        <div class="player-top">
            <div class="top-left">
              <div class="mode loop" ref="mode" @click="mode" ></div>
              <p v-if="this.modeType===0">顺序播放</p>
              <p v-else-if="this.modeType===1">单曲循环</p>
              <p v-else>随机播放</p>
            </div>
            <div class="top-right">
              <div class="del" @click="delAll">
              </div>
            </div>
        </div>
        <div class="player-middle">
          <ScrollView ref="scrollView">
            <ul ref="play">
              <li class="item" v-for="(value,index) in songs" :key="value.id" @click="selectMusic(index)">
                <div class="item-left">
                  <div class="item-play" @click="play" v-show="currentIndex===index"></div>
                  <p>{{value.name}}</p>
                </div>
                <div class="item-right">
                  <div class="item-favorite"></div>
                  <div class="item-del" @click.stop="del(index)"></div>
                </div>
              </li> 
               

            </ul>
          </ScrollView>
        </div>
        <div class="player-bottom">
          <p @click.stop='close'>关闭</p>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';
import mode from '../../store/modeType'
import ScrollView from '../../components/ScrollView'
import Velocity from 'velocity-animate';
import 'velocity-animate/velocity.ui';
export default {
  name: 'ListPlayer',
  components:{
    ScrollView
  },
  computed:{
      ...mapGetters([
          'isPlaying',
          'modeType',
          'isShowListPlayer',
          'songs',
          'currentIndex'
      ]),

  },
  methods:{
     ...mapActions([
          'setIsPlaying',
          'setModeType',
          'setListPlayer',
          'setDelSong',
          'setCurrentIndex'
      ]),
     play(){
          this.setIsPlaying(!this.isPlaying);
     },
     mode(){
          if(this.modeType===mode.loop){
              this.setModeType(mode.one);
          }else if(this.modeType===mode.one){
              this.setModeType(mode.random);
          }else if(this.modeType===mode.random){
              this.setModeType(mode.loop);
          }
     },
    close(){
      this.setListPlayer(false);
    },
    enter: function (el, done) {
      Velocity(el, 'transition.slideUpBigIn', { duration: 300 })
    },
    leave: function (el, done) {
      Velocity(el, 'transition.slideDownBigOut', { duration: 300 })
    },
    del(index){
      this.setDelSong(index);
    },
    delAll(){
      this.setDelSong();
    },
    selectMusic(index){
      this.setCurrentIndex(index);
    }
  },
   
   watch:{
      isPlaying(newValue,oldValue){
          if(newValue){
              this.$refs.play.classList.add('active');
            //  console.log(this.$refs);
          }else{
              this.$refs.play.classList.remove('active');
          }
      },
      modeType(newValue,oldValue){
           if(newValue===mode.loop){
                this.$refs.mode.classList.remove('random');
                this.$refs.mode.classList.add('loop');
          }else if(newValue===mode.one){
                this.$refs.mode.classList.remove('loop');
                this.$refs.mode.classList.add('one');
          }else if(newValue===mode.random){
                this.$refs.mode.classList.remove('one');
                this.$refs.mode.classList.add('random');
          }
      },
      isShowListPlayer(newValue,oldValue){
        if(newValue){
          this.$refs.scrollView.refresh();
        }
      }
    }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
.list-player{
  overflow: hidden;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  @include bg_sub_color();
  .player-warpper{
    width: 100%;
    .player-top{
      width: 100%;
      height: 100px;
      display: flex;
      justify-content: space-between;
      padding:0 20px;
      box-sizing: border-box;
      .top-left{
        display: flex;
        align-items: center;
        .mode{
          width: 56px;
          height: 56px;
          // @include bg_img('../../assets/images/small_loop');
          margin-right: 20px;
           &.loop{
                @include bg_img('../../assets/images/small_loop');
            }
            &.one{
                 @include bg_img('../../assets/images/small_one');
            }
            &.random{
                 @include bg_img('../../assets/images/small_shuffle');
            }
        }
        p{
          @include font_size($font_medium_s);
          @include font_color();
        }
      }
      .top-right{
        .del{
            width: 84px;
            height: 84px;
            @include bg_img('../../assets/images/small_del')
        }
      }
    }
    
    .player-middle{
       height: 700px;
       overflow: hidden;
       ul{
         &.active{
           .item{
                  .item-play{
                     @include bg_img('../../assets/images/small_pause');
                  }
            } 
          } 
       }
      .item{
        height: 100px;
        padding:0 20px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #ccc;
        .item-left{
          display: flex;
          align-items: center;
           .item-play{
             margin-right: 20px;
             width: 56px;
             height: 56px;
            @include bg_img('../../assets/images/small_play');
          }
          p{
            @include font_color();
            @include font_size($font_medium_s);
          }

        }
        .item-right{
          display: flex;
          align-items: center;
          margin-right: 20px;
          .item-favorite{
            width: 56px;
            height: 56px;
            @include bg_img('../../assets/images/small_favorite')

          }
          .item-del{
            width: 52px;
            height: 52px;
            @include bg_img('../../assets/images/small_close')

          }
        }
       
      }
    }
    .player-bottom{
      p{
        border-top: 1px solid #ccc;
        line-height: 100px;
        text-align: center;
        color:#fff;
        @include font_size($font_medium);
        @include bg_color();
      }
    }
  
  }
}

</style>