<template>
  <div class="player-bottom">
      <div class="player-progress">
          <span ref="eleCurrentTime">00:00</span>
          <div class="progress-bar" @click="progressClick" ref="progressBar">
              <div class="progress-line" ref="progressLine">
                  <div class="progress-dot">

                  </div>
              </div>
          </div>
          <span ref="eleTotalTime">03:00</span>
      </div>
      <div class="player-control">
          <div class="mode loop" @click="mode" ref="mode"></div>
          <div class="prev" @click="prev"></div>
          <div class="play" @click="play" ref="play"></div>
          <div class="next" @click="next"></div>
          <div class="favorite"></div>
      </div>
  </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import mode from '../../store/modeType'
export default {
  name: 'PlayerBottom',
  methods:{
      ...mapActions([
          'setIsPlaying',
          'setModeType',
          'setCurrentIndex',
          'setCurrentTime'
      ]),
      play(){
          this.setIsPlaying(!this.isPlaying);
      },
      mode(){
          if(this.modeType===mode.loop){
              this.setModeType(mode.one);
          }else if(this.modeType===mode.one){
              this.setModeType(mode.random);
          }else if(this.modeType===mode.random){
              this.setModeType(mode.loop);
          }
      },
      prev(){
          this.setCurrentIndex(this.currentIndex-1);
      },
      next(){
          this.setCurrentIndex(this.currentIndex+1);
      },
      formartTime(time) {
        // 2.得到两个时间之间的差值(秒)
        let differSecond = time
        // 3.利用相差的总秒数 / 每一天的秒数 = 相差的天数
        let day = Math.floor(differSecond / (60 * 60 * 24))
        day = day >= 10 ? day : '0' + day
        // 4.利用相差的总秒数 / 小时 % 24;
        let hour = Math.floor(differSecond / (60 * 60) % 24)
        hour = hour >= 10 ? hour : '0' + hour
        // 5.利用相差的总秒数 / 分钟 % 60;
        let minute = Math.floor(differSecond / 60 % 60)
        minute = minute >= 10 ? minute : '0' + minute
        // 6.利用相差的总秒数 % 秒数
        let second = Math.floor(differSecond % 60)
        second = second >= 10 ? second : '0' + second
        return {
            day: day,
            hour: hour,
            minute: minute,
            second: second
        }
      },
      progressClick(e){
          //1.计算进度条位置
          //整个离最左边
          let normalLeft=e.target.offsetLeft;
          //点击离最左边
          let eventLeft=e.pageX;
          //点击的进度条长度
          let clickLeft=eventLeft-normalLeft;
          //进度条背景的总长,e.target可能点到bar也可能点到line
        //   let progressWidth=e.target.offsetWidth;
          let progressWidth=this.$refs.progressBar.offsetWidth;
          //点击的占百分之多少
          let value=clickLeft/progressWidth;
          this.$refs.progressLine.style.width=value*100+"%";
        //  console.log(eventLeft,normalLeft,clickLeft,progressWidth,value);
        
        //2.计算什么时候开始播放,需要共享audio的currentTime
          this.setCurrentTime(value*this.totalTime);
      }

  },
  computed:{
      ...mapGetters([
          'isPlaying',
          'modeType',
          'currentIndex',
      ])
  },
  watch:{
      isPlaying(newValue,oldValue){
          if(newValue){
              this.$refs.play.classList.add('active');
          }else{
              this.$refs.play.classList.remove('active');
          }
      },
      modeType(newValue,oldValue){
           if(newValue===mode.loop){
                this.$refs.mode.classList.remove('random');
                this.$refs.mode.classList.add('loop');
          }else if(newValue===mode.one){
                this.$refs.mode.classList.remove('loop');
                this.$refs.mode.classList.add('one');
          }else if(newValue===mode.random){
                this.$refs.mode.classList.remove('one');
                this.$refs.mode.classList.add('random');
          }
      },
      totalTime(newValue,oldValue){
          let time=this.formartTime(newValue);
          this.$refs.eleTotalTime.innerHTML=time.minute+':'+time.second;
      },
      currentTime(newValue,oldValue){
          //1.当前播放时间
          let time=this.formartTime(newValue);
          this.$refs.eleCurrentTime.innerHTML=time.minute+':'+time.second;
          //2.进度条长度
          this.$refs.progressLine.style.width=newValue/this.totalTime*100 +'%';
      },
     
    },
  props:{
        totalTime:{
            type:Number,
            default:0,
            required:true,
        },
        currentTime:{
            type:Number,
            default:0,
            required:true,
        }
    }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
.player-bottom{
    width: 100%;
    position: fixed;
    bottom:0;
    left: 0;
    .player-progress{
        width: 80%;
        margin: 0 auto;
        display: flex;
        align-items:center;
        justify-content: space-around;
        span{
            @include font_size($font_small);
            @include font_color();
        }
        .progress-bar{
            width: 100%;
            height: 10px;
            background-color: #fff;
            margin: 0 20px;

            .progress-line{
                width: 0;
                height: 100%;
                background-color: #aaa;
                position: relative;

                .progress-dot{
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    background-color: #aaa;
                    position: absolute;
                    top: 50%;
                    left: 100%;
                    transform: translate(-50%,-50%);
                }
            }
        }
    }
    .player-control{
        width: 80%;
        height: 100px;
        display: flex;
        justify-content: space-between;
        padding: 50px 0;
        align-items: center;
        margin: 0 auto;
        div{
            width: 100px;
            height: 100px;
        }
        .mode{
            // @include bg_img('../../assets/images/loop');
            &.loop{
                @include bg_img('../../assets/images/loop');
            }
            &.one{
                 @include bg_img('../../assets/images/one');
            }
            &.random{
                 @include bg_img('../../assets/images/shuffle');
            }
        } 
        .prev{
            @include bg_img('../../assets/images/prev');
        } 
        .play{
            //播放符号代表没播着
            @include bg_img('../../assets/images/play');
            &.active{
                 @include bg_img('../../assets/images/pause');
            }
        } 
        .next{
            @include bg_img('../../assets/images/next');
        } 
        .favorite{
            @include bg_img('../../assets/images/un_favorite');
        }
    }
}
</style>