<template>
    <transition 
    v-bind:css="false"
    v-on:enter="enter"
    v-on:leave="leave"
    >
        <div class="mini-player" v-show="isShowMiniPlayer">
            <div class="player-warpper">
                <div class="player-left" @click="showNormalPlayer">
                    <img :src="currentSong.picUrl" ref="cd">
                    <div class="player-title">
                        <h3>{{currentSong.name}}</h3>
                        <p>{{currentSong.singer}}</p>
                    </div>
                </div>
                <div class="player-right">
                    <div class="play" @click="play"  ref="play"></div>
                    <div class="list" @click.stop='showList'></div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';
import Velocity from 'velocity-animate';
import 'velocity-animate/velocity.ui';
export default {
  name: 'MiniPlayer',
  methods:{
      ...mapActions([
          'setFullScreen',
          'setMiniPlayer',
          'setIsPlaying',
          'setListPlayer'
         ]),
       showNormalPlayer(){
           this.setFullScreen(true);
       },
      showList(){
          //调用父组件Player的showList方法
          this.setListPlayer(true);
      },
       enter: function (el, done) {
         Velocity(el, 'transition.bounceUpIn', { duration: 300 })
        },
       leave: function (el, done) {
            Velocity(el, 'transition.bounceDownOut', { duration: 300 })
        },
        play(){
          this.setIsPlaying(!this.isPlaying);
        }
  },
  computed:{
      ...mapGetters([
          'isShowMiniPlayer',
          'isPlaying',
          'currentSong'
      ]),

  },
   watch:{
      isPlaying(newValue,oldValue){
          if(newValue){
              this.$refs.play.classList.add('active');
              this.$refs.cd.classList.add('active');
          }else{
              this.$refs.play.classList.remove('active');
              this.$refs.cd.classList.remove('active');
          }
      }
  }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
.mini-player{
    width: 100%;
    height: 120px;
    position: fixed;
    bottom: 0;
    left: 0;
    @include bg_color();
    .player-warpper{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .player-left{
            display: flex;
            padding-left:50px;
            box-sizing: border-box;
            img{
                width: 100px;
                height: 100px;
                border-radius: 50%;
                animation: sport 5s linear  infinite;
                animation-play-state:paused;
                &.active{
                     animation-play-state:running;
                }
            }
            .player-title{
                margin-left: 40px;
                display: flex;
                flex-direction: column;
                justify-content: center;

                h3{
                    @include font_size($font_medium);
                    // @include font_color();
                    color:#fff;
                }
                p{
                    @include font_size($font_medium_s);
                    // @include font_color();
                    color:#fff;

                }
            }
        }
        .player-right{
            display: flex;
            align-items: center;
            .play{
                 width: 80px;
                height: 80px;
                @include bg_img('../../assets/images/play');
                &.active{
                 @include bg_img('../../assets/images/pause');
            } 
            }
            .list{
                 width: 120px;
                height: 120px;
                @include bg_img('../../assets/images/list');
            }
        }
    }
}
@keyframes sport{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
</style>