<template>
  <transition 
  v-bind:css="false"
  v-on:enter="enter"

  v-on:leave="leave"
  >
    <div class="normal-player" v-show="this.isFullScreen">
      <div class="player-warpper">
        <PlayerHeader></PlayerHeader>
        <PlayerMiddle></PlayerMiddle>
        <PlayerBottom :totalTime="totalTime" :currentTime="currentTime"></PlayerBottom>
      </div>
      <div class="player-bg">
          <img :src="currentSong.picUrl" alt="">
      </div>
    </div>
  </transition>
</template>

<script>
import PlayerHeader from './PlayerHeader';
import PlayerMiddle from './PlayerMiddle';
import PlayerBottom from './PlayerBottom';
import {mapGetters,mapActions} from 'vuex'
import Velocity from 'velocity-animate';
import 'velocity-animate/velocity.ui';
export default {
  name: 'NormalPlayer',
  components:{
      PlayerHeader,
      PlayerMiddle,
      PlayerBottom
  },
  
  computed:{
    ...mapGetters([
      'isFullScreen',
      'currentSong'
    ])
  },
  methods:{
    ...mapActions([
      'getSongLyric'
    ]),
    enter: function (el, done) {
      Velocity(el, 'transition.slideLeftIn', { duration: 300 })
    },
    leave: function (el, done) {
      Velocity(el, 'transition.slideLeftOut', { duration: 300 })
    },
  },
  watch:{
    currentSong(newValue,oldValue){
      if(newValue.id===undefined){
        return;
      }
      this.getSongLyric(newValue.id);
    }
  },
  props:{
    totalTime:{
      type:Number,
      default:0,
      required:true,
    },
    currentTime:{
      type:Number,
      default:0,
      required:true,
    }
  }
  
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
.normal-player{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @include bg_sub_color();
  
  .player-warpper{
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 999;
  }
  .player-bg{
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      img{
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        filter:blur(10px);
      }
    }
}

</style>